// }

.more-info-result {
  width: 58px;
  height: 20px;
  color: #fff;
  font-family: Lato;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  opacity: 0.54;
  position: relative;
}

